import Vue from 'vue'
import VueRouter from 'vue-router'
import MainPage from "@/pages/MainPage"
import NotFoundPage from "@/pages/NotFoundPage"
import ProductPage from "@/pages/ProductPage"
import CartPage from "@/pages/CartPage"
import OrderPage from "@/pages/OrderPage"
import OrderInfoPage from "@/pages/OrderInfoPage"

Vue.use(VueRouter); //сообщ vue что собираемся использовать vue-router

//создаем массив где будут храниться маршруты в виде объекта
const routes = [
    {name:'main',component:MainPage, path:'/'}, //указ ключ-е название для страницы,св-во компонент-передаем экз какого-то компонента,котор будет отрисов когда активно, путь в адрес строке
    {name:'product', component:ProductPage, path:'/product/:id'},
    {name:'cart', component:CartPage, path:'/cart'},
    {name:'order', component:OrderPage, path:'/order'},
    {name:'orderInfo', component:OrderInfoPage, path:'/order/:id'},
    {name:'notFound', component:NotFoundPage, path:'*'},
];

//создаем новый экземпляр routera и экспортир его
const router= new VueRouter({
    routes      //подключ массив к экзмепляру routera
});

export default router;