<template>
     <li class="catalog__item" :product="product" :key="product.id">
            <router-link class="catalog__pic" :to="{name:'product', params:{id:product.id}}">
              <img :src="product.image"  alt="product.title">
            </router-link>

            <h3 class="catalog__title">
              <a href="#">
{{ product.title }}
              </a>
            </h3>

            <span class="catalog__price">
{{ product.price | numberFormat }}
            </span>

            <ul class="colors colors--black">
              <li class="colors__item"  v-for="color in colors" :key="color.index">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" :value="color.id" v-model="colors" >
                  <span class="colors__value" :style="`background-color:${color};`">
                  </span>
                </label>
              </li>
            </ul>
          </li>
</template>

<script>

import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";


export default{
  props:['product'],
  
  computed:{
    colors(){
      const colors = this.product.colors;
      return colors.map((el)=> el.code);
    },
  },
  filters:{
    numberFormat
  },
  methods:{
gotoPage,
  },
   
}
</script>