<template>
 <div class="item__row">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар"  @click.prevent="desincrement">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" :value="productAmount"  @input.="$emit('update:product-amount', $event.target.value)" :disabled="productAddSending">

                <button type="button" aria-label="Добавить один товар"   @click.prevent="increment">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit">
                В корзину
              </button>
            </div>
</template>

<script>

export default{
props:['productAmount', 'productAddSending'],

    methods:{
        increment(){
  this.$emit('update:product-amount', this.productAmount + 1)
      },
desincrement(){
  if(this.productAmount>1){
  this.$emit('update:product-amount', this.productAmount - 1)   
  }   
      },
    },
    
}

</script>